@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
body {
  margin: 0;
  font-family: 'Lexend Deca', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Lexend Deca', sans-serif;
}

input {
  font-family: 'Lexend Deca', sans-serif;
}

button {
  font-family: 'Lexend Deca', sans-serif;
}

.app-container {
  margin: auto;
  width: 80%;
}

.spell-list-header ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style-type: none;
}

.spell-list-header ul li {
  margin: 0;
  padding: 1em 0;
  width: 7em;
}

.spell-list-header ul li:first-child {
  text-align: center;
}

:root {
  --header-bg: crimson;
  --header-text: white;
}

.spell-container {
  margin: 0 0 0.5em;
}

.spell-header {
  display: flex;
  justify-content: space-between;
  /* border-radius: .5em .5em 0 0; */
  background: crimson;
  background: var(--header-bg);
  color: white;
  color: var(--header-text);
}

.spell-header ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style-type: none;
}

.spell-header ul li {
  padding: 1em 0;
}

.spell-header ul li.spell-header-level {
  width: 7em;
  text-align: center;
}

.spell-header .toggle {
  justify-content: flex-end;
  width: 7em;
  padding: 1em 0;
  text-align: center;
}

.spell-desc {
  padding: 1em;
}

p {
  margin: 0;
}

p.type {
  margin: 0 0 1em;
  font-style: italic;
}

.main {
  margin: 0;
}

.descriptor {
  font-weight: bold;
}

.hidden {
  display: none;
}

:root {
  --form-button: crimson;
  --form-button-text: white;
}

.spell-form form {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.spell-form input {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  margin: 0;
  padding: 0 1em;
}

.spell-form button {
  background: crimson;
  background: var(--form-button);
  color: white;
  color: var(--form-button-text);
  border: none;
  cursor: pointer;
  padding: 1em 2em;
}

